.main-project{
    margin-top: 8em;
    display: grid;
    place-items: center;
    background-color: #050505;
    color: white;
    clip-path: ellipse(93% 100% at 40.97% 100%);

    .project-head{
        color: white;
        font-size: 2.5rem;
        font-weight: 700;
        margin-bottom: 2em;
        margin-top: 2em;
    }
    .project-flex{
        padding: 10px;
        display: flex;
        .tilted-para{
            transform: translate3d(0px, 0px, 0px) perspective(1px) skew(0.0294deg, 0deg);
            padding-top: 0.5em;
            padding-bottom:0.5em ;
            transform: scaleX(1.5);
            font-size: 5rem;
            font-weight: 900;
            position: relative;
            transition: all 200ms;
            &::before{
                content: '';
                position: absolute;
                width: 0px;
                height: 0px;
                background-color: transparent;
                border-top: 5px solid red;
                border-right: 5px solid red;
                bottom: 80px;
                left: -50px;
                transform: rotate(45deg);
            }
            &:hover{
                color:#FF0066
            }
            &:hover::before{
                width: 30px;
                height: 30px;
            }
        }
        .solid {
            margin: 0 -555px;
            border: 0;
            border-top: 1px solid #c9c7c7;
          }
    }
}

@media all and (max-width:876px){
    .main-project{
        clip-path: ellipse(192% 100% at 41.48% 100%);
    
        .project-head{
            font-size: 36px;
            margin-bottom: 1em;
            margin-top: 4em;
        }
        .project-flex{
            padding: 10px;
            display: flex;
            .tilted-para{
                padding-top: 1.5em;
                padding-bottom:0.5em ;
                transform: scaleX(1);
            }
        }
    }
}

@media all and(max-width:576px){
    .main-project{
        margin-top: 1em;
        .project-head{
            margin-bottom: 1em;
            margin-top: 4em;
        }
        .project-flex{
            padding: 10px;
            display: flex;
            .tilted-para{
                padding-top: 1.5em;
                padding-bottom:0.5em ;
                transform: scaleX(1);
                font-size: 2.6rem;
                &::before{
                    width: 20px;
                    height: 20px;
                    bottom: 44px;
                    left: -30px;
                }
            }
        }
    }    
}