.main-project {
  margin-top: 8em;
  display: grid;
  place-items: center;
  background-color: #050505;
  color: white;
  -webkit-clip-path: ellipse(93% 100% at 40.97% 100%);
          clip-path: ellipse(93% 100% at 40.97% 100%);
}
.main-project .project-head {
  color: white;
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 2em;
  margin-top: 2em;
}
.main-project .project-flex {
  padding: 10px;
  display: flex;
}
.main-project .project-flex .tilted-para {
  transform: translate3d(0px, 0px, 0px) perspective(1px) skew(0.0294deg, 0deg);
  padding-top: 0.5em;
  padding-bottom: 0.5em;
  transform: scaleX(1.5);
  font-size: 5rem;
  font-weight: 900;
  position: relative;
  transition: all 200ms;
}
.main-project .project-flex .tilted-para::before {
  content: "";
  position: absolute;
  width: 0px;
  height: 0px;
  background-color: transparent;
  border-top: 5px solid red;
  border-right: 5px solid red;
  bottom: 80px;
  left: -50px;
  transform: rotate(45deg);
}
.main-project .project-flex .tilted-para:hover {
  color: #FF0066;
}
.main-project .project-flex .tilted-para:hover::before {
  width: 30px;
  height: 30px;
}
.main-project .project-flex .solid {
  margin: 0 -555px;
  border: 0;
  border-top: 1px solid #c9c7c7;
}

